@-webkit-keyframes jump {
  0% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0); }
  50% {
    -webkit-transform: translate(0, -0.3rem);
    transform: translate(0, -0.3rem); }
  100% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0); } }

